import React from 'react'
import { Formik, Form, Field, ErrorMessage } from 'formik'
import * as Yup from 'yup'
import { Container } from '../Container'
import axios from 'axios'

const fieldSchema = Yup.object().shape({
  fullname: Yup.string()
    .required('Full name is required'),
  email: Yup.string()
    .email('Email is invalid')
    .required('Please fill in your email'),
  message: Yup.string()
    .required(''),
})

const FeedbackForm: React.FC<{}> = () => (
  <Formik
    initialValues={{
      fullname: '',
      email: '',
      message: '',
    }}
    validationSchema={ fieldSchema }
    onSubmit = { async (values, { setSubmitting, resetForm }) => {
      try {
        await axios({
          method: 'POST',
          url: `https://formspree.io/f/${process.env.GATSBY_FORMSPREE_ENDPOINT}`,
          headers: { 'Content-Type': 'application/json', },
          data: JSON.stringify(values)
        })
        setSubmitting(false);
        setTimeout(() => resetForm(), 3000)
      } catch (err) {
        console.warn('Something went wrong!')
      }
    }}
  >
    {(isSubmitting) =>
      <Container>
        <Form noValidate>
          <Field type="text" name="_gotcha" style={{ display: `none`}} />
          <label htmlFor="name">Full Name</label>
          <Field name="name" id="fullname" placeholder="What's your Name?" />
          <label htmlFor="email">Email</label>
          <Field type="email" name="_replyto" id="email" placeholder="yourmail@domain.com" />
          <label htmlFor="message">Message</label>
          <Field name="message" id="message" component="textarea" 
            placeholder="Please leave your message here..."
          />
          <button type="submit">Submit</button>
        </Form>
      </Container>
    }
  </Formik>
)

export default FeedbackForm

